&__slide1-dots {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 70%;
  height: 40%; }

&__slide2-dots, &__slide3-dots {
  position: absolute;
  top: 15%;
  right: 0;
  width: 75%;
  height: 40%; }

