&__decorations {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;

  &-planet-sm {
    position: absolute;
    left: 9.1%;
    bottom: 11%;
    width: 8.5%; }

  &-planet-lg {
    position: absolute;
    top: 27%;
    left: 43.2%;
    width: 19.6%; }

  &-planet {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 58.2%; }

  &-spaceman {
    opacity: 0;
    transition: 2s opacity;

    &--active {
      opacity: 1;
      transition: 2s opacity; }

    &--show {
      opacity: 1;
      transition: 0s opacity; }

    &--hide {
      opacity: 0;
      transition: 0s opacity; } }

  &-stand {
    position: absolute;
    bottom: 6.5%;
    left: 33.8%;
    width: 28.4%;

    @media (max-height: 670px) and (min-width: 1230px) {
      max-width: 346px; } }

  &-take {
    position: absolute;
    bottom: 6.40%;
    left: 33.9%;
    width: 38.83%;

    @media (max-height: 670px) and (min-width: 1230px) {
      max-width: 475px; }

    img {
      max-width: 100%; } }

  &-flight {
    position: absolute;
    z-index: 3000;
    top: 45%;
    right: 65%;
    width: 20%;
    padding-top: 25%;
    background: {
      //image: url("/images/tobacco-spaceman-flight.png")
      repeat: no-repeat;
      size: contain;
      position: center center; }

    img {
      position: absolute;
      left: 0;
      top: 0;
      max-width: 100%; }

    //animation:
    //  name: spaceman-flight
    //  duration: 4s
    //  timing-function: linear
    //  iteration-count: 1
    //  fill-mode: forwards

    &--pos-1 {
      top: 29%;
      right: 50%;
      width: 27%;
      padding-top: 33%; }

    &--seq-1 {
      top: 95%;
      right: 95%; }

    &--seq-2 {
      top: 84%;
      right: 86%; }

    &--seq-3 {
      top: 76%;
      right: 80%; }

    &--seq-4 {
      top: 65%;
      right: 70%; }

    &--seq-5 {
      top: 58%;
      right: 64%; }

    &--seq-6 {
      top: 48%;
      right: 56%;
      width: 23%; }

    &--seq-7 {
      top: 29%;
      right: 44%;
      width: 30%; } }




  &-surface {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%; }

  &-planet-md {
    position: absolute;
    top: 27.1%;
    right: 8.2%;
    width: 16.5%; }

  &-planet-md-down {
    position: absolute;
    top: 40.1%;
    right: 19.1%;
    width: 15.7%; } }

&__leaf {
  position: absolute;
  bottom: 59.3%;
  right: 10.7%;
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s;

  &--active {
    visibility: visible;
    opacity: 1; }

  &-icon {
    width: 100%;
    height: 100%;
    max-width: 100%; }

  &-line {
    //position: absolute
    //top: 51%
    //left: 100%
    padding-bottom: 10px;
    border-bottom: 1px solid $text-color;
    //margin-left: 13px
    letter-spacing: 4px;
    text-transform: uppercase;
    white-space: nowrap; } }
