.enter {
  height: 100vh;
  position: relative;
  overflow: hidden;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000; }

  &__dots {
    padding: 42px;
    height: 100%; }

  &__dots-wrapper {
    position: relative;
    height: 100%; }

  &__dot {
    background: #fff;
    height: 3px;
    width: 3px;
    position: absolute;
    border-radius: 50%;
    margin-top: -1.5px;
    margin-left: -1.5px; }

  &__head {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;

    img {
      position: absolute;
      display: block;
      height: 100%;
      left: 50%;
      transform: translateX(-50%); } }

  &__scene {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

  &__wrapper {
    position: relative;
    display: table;
    height: 100vh;
    width: 100%; }

  &__inner {
    display: table-cell;
    vertical-align: middle;
    padding: 0 30px; }

  &__title {
    color: #fff;
    font-family: sans-serif;
    font-size: 38px;
    font-weight: 600;
    text-align: center;
    margin: 35px 0; }

  &__social {
    margin-top: 108px;
    margin-bottom: 40px;
    padding: 13px 0; }

  &__buttons {
    max-width: 310px;
    margin: 0 auto;

    .button + .button {
      margin-top: 10px; } }

  &__creator {
    position: absolute;
    left: 0;
    top: 50%;
    color: #fff;
    font-family: $font-gotham-pro;
    text-transform: uppercase;
    font-size: 9px;
    letter-spacing: 0.4em;
    transform: rotateZ(-90deg) translateX(-50%);
    transform-origin: 0 0;

    a {
      display: inline-block;
      padding: 14px; }

    svg {
      fill: #fff;
      vertical-align: middle; } }

  &__right-line {
    position: absolute;
    top: 50%;
    right: 18px;
    width: 3px;
    height: 174px;
    background: #fff;
    margin-top: -87px; } }
