.slider {
  height: 100%;
  overflow: hidden;

  &__container {
    height: 100%;
    transition: 1s transform; }

  &__slide {
    position: relative;
    height: 100%; }

  &__bg-scenes {
    height: 100%; }

  &__bg-scene {
    background-position: center center;
    background-size: cover;
    height: 100%;
    opacity: 0;
    transition: 1.2s opacity;

    &--active {
      opacity: 1; } } }
