.main-mobile {
  height: 100%;

  &__main {
    position: relative;
    display: table;
    height: 100%;
    width: 100%;
    overflow: hidden; }

  &__main-inner {
    display: table-cell;
    vertical-align: middle; }

  &__main-img {
    padding: 0 40px;
    max-width: 850px;
    margin: 0 auto;
    position: relative;
    z-index: 1;

    img {
      max-width: 100%; } }


  &__line {
    position: absolute;
    left: 50%;
    bottom: 5%;
    width: 1px;
    height: 8%;
    background: #fff; }

  &__circle-1 {
    width: 53%;
    top: 0;
    left: 50%;
    transform: translate(-50%, -45%); }

  &__circle-2 {
    width: 90%;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 40%); }

  &__planet-1 {
    width: 70%;
    top: 15%;
    left: -5%; }

  &__planet-2 {
    width: 43%;
    right: 0;
    bottom: 0;
    transform: translateX(45%); }

  &__planet-3 {
    width: 25%;
    left: 15%;
    top: 5%; }

  &__footer {
    position: relative;
    padding-top: 65%;
    overflow: hidden; }


  &__dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80%; } }
