@keyframes spaceman-flight {
  0% {
    transform: translate(0, 0) scale(0.5) rotate(0deg); }
  50% {
    transform: translate(20.95vw, -27.7vh) scale(0.75) rotate(15deg); }
  100% {
    transform: translate(41.9vw, -55.4vh) scale(1) rotate(-5deg); } }

@keyframes mine-flight {
  0% {
    transform: translate(0, 0) rotate(0deg); }
  100% {
    transform: translate(50px, 75px) rotate(15deg); } }

@keyframes flask-flight {
  0% {
    transform: translate(0, 0) rotate(0deg); }
  100% {
    transform: translate(50px, -95px) rotate(-15deg); } }

@keyframes hose-flight {
  0% {
    transform: translate(0, 0) rotate(0deg); }
  100% {
    transform: translate(30px, -75px) rotate(90deg); } }

@keyframes forceps-right-flight {
  0% {
    transform: translate(0, 0) rotate(0deg); }
  100% {
    transform: translate(-30px, 75px) rotate(120deg); } }

@keyframes forceps-left-flight {
  0% {
    transform: translate(0, 0) rotate(0deg); }
  50% {
    transform: translate(-4vw, 20vh) rotate(40deg); }
  100% {
    transform: translate(0px, 0px) rotate(0); } }


@keyframes universe-spaceman-flight {
  0% {
    transform: translateX(0) rotate(0); }
  50.5% {
    transform: translateX(66.2vw) rotate(-15deg); }
  100% {
    transform: translateX(112.4vw) rotate(0); } }


@keyframes taste-12-flight {
  0% {
    opacity: 0; }
  10% {
    opacity: 1; }
  100% {
    opacity: 1;
    transform: scale(6) translate(-10vw, 20vh); } }

