@keyframes hand-shake {
  0% {
    transform: rotate(-90deg);
    left: 0%;
    bottom: 2%; }
  100% {
    transform: rotate(0);
    left: 63.8%;
    bottom: 10%; } }

@keyframes calendar-show {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
