.contact {
  display: table;
  width: 100%;
  height: 100%;
  font-size: 0;

  &__wrapper {
    display: table-cell;
    height: 100%;
    vertical-align: middle;

    @include media-1440 {
      padding-top: 113px;
      vertical-align: top; } }

  &__astro {
    display: inline-block;
    width: 50%;
    height: 547px;
    padding: 0 8px;
    text-align: center;
    vertical-align: bottom;
    position: relative;

    @include media-1440 {
      box-sizing: border-box;
      padding-right: 38px;
      text-align: right; }

    img {
      max-width: 100%;
      max-height: 100%; } }

  &__astro-img {
    opacity: 0;
    transition: 0.3s opacity;

    &--active {
      opacity: 1; } }

  &__astro-animate {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    @include media-1440 {
      box-sizing: border-box;
      padding-right: 38px; } }

  &__contact-form {
    display: inline-block;
    width: 33.7%;
    padding: 0 8px; }

  &__distribute {
    display: inline-block;
    width: 15.6%;
    padding: 0 8px;
    vertical-align: top;
    padding-top: 85px; } }

