.where-buy {
  display: table;
  margin: 5% 0;
  height: 90%;
  width: 100%;

  &__inner {
    //display: table-cell
 }    //vertical-align: middle

  &__list {
    padding: 0 8px;
    width: ((100% / 12) * 8);
    margin-left: ((100% / 12) * 3);
    font-size: 0;

    @include clearfix; }

  &__item {
    //float: left
    display: inline-block;
    width: (100% / 4);
    padding: 20px 0;
    vertical-align: top; } }

.where-buy-item {

  &__city {
    font-family: $font-proxima-nova;
    font-weight: 800;
    font-size: 10px;
    line-height: 1.2;
    margin-bottom: 20px;
    color: #646464;
    text-transform: uppercase;
    letter-spacing: 0.4em;
    @include media-992 {
      letter-spacing: 0.15em; } }

  &__name {
    font-family: $font-proxima-nova;
    font-weight: 400;
    font-size: 11px;
    line-height: 22px;
    color: #fff;
    @include media-992 {
      font-size: 14px; } }

  &__phone {
    font-family: $font-proxima-nova;
    font-weight: 400;
    font-size: 11px;
    line-height: 22px;
    color: #fff;
    @include media-992 {
      font-size: 14px; }

    a {
      text-decoration: none; } }

  &__social {
    margin-top: 12px;
    font-size: 20px;

    .icon-facebook:before {
      margin: 0;
      width: 0.7em; }

    a {
      text-decoration: none; } } }
