.main {
  height: 100%;
  overflow: hidden;

  &__skip {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    top: 40px;
    right: 30px;
    z-index: 2;
    transition: 0.5s opacity, 0s visibility 0.5s;

    &:after {
      content: "";
      display: block;
      height: 1px;
      width: 100%;
      background: #fff; }

    &--active {
      visibility: visible;
      opacity: 1;
      transition: 0.5s opacity, 0s visibility 0s; } }

  &__wrapper {
    height: 100%;
    transition: {
      duration: 2s;
      property: transform;
      timing-function: ease-in-out; }

    &--section-2 {
      transform: translateY(-140%); }

    &--section-3 {
      transform: translateY(-200%); }

    &--fast-scroll {
      transition: {
        duration: 0s; } } }

  &__section {
    display: table;
    width: 100%;
    height: 100%;
    position: relative; }

  &__section-inner {
    display: table-cell;

    &--middle {
      vertical-align: middle; }

    &--bottom {
      vertical-align: bottom; } }

  &__video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: 100%;
    z-index: 0;
    transform: translateX(-50%) translateY(-50%);
    background: #000; }

  &__main-text {
    max-width: 50%;
    margin: 0 auto;
    position: relative;
    transform: scale(0);
    transform-origin: 50% 50%;

    &--active {
      transform: scale(1);
      transition: 1s transform; } }

  &__line {
    position: absolute;
    background: #fff;
    left: 50%;
    width: 1px;
    height: 0;

    &--active {
      height: 90px;
      transition: 2s height; } }


  &__description {
    width: 455px;
    margin: 0 auto;
    margin-bottom: -5%;
    position: relative;
    color: #fff;
    font-family: $font-proxima-nova;
    font-weight: 700;
    font-size: 10px;
    line-height: 3em;
    letter-spacing: 0.4em;
    text-transform: uppercase;
    text-align: center;
    transform: translateY(400px);
    transition: 1.5s transform;

    &--active {
      transform: translateY(0px); }

    &--hide {
      transform: translateY(-400px); }

    &--fast-anim {
      transition: {
        duration: 0s; } } }

  &__space-man {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: none; }

  &__3d-scene-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

  &__3d-scene-mountains {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    img {
      position: absolute;
      top: 10%;
      left: 0;
      width: 100%; } }

  &__3d-scene {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent; }

  &__move-next {
    position: absolute;
    bottom: 40px;
    left: 50%;
    width: 40px;
    height: 40px;
    padding: 0;
    border: none;
    margin-left: -20px;
    background: transparent url('../images/icon-slide-down.svg') no-repeat center;
    background-size: cover;
    opacity: 0;
    animation: {
      name: slide-down;
      duration: 1.5s;
      iteration-count: infinite; }

    &:hover {
      background-color: transparent; } } }

@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(0); }
  50% {
    opacity: 1; }
  100% {
    opacity: 0;
    transform: translateY(20px); } }
