.distribute {

  &__title {
    display: inline-block;
    font-family: $font-proxima-nova;
    font-size: 10px;
    font-weight: 700;
    line-height: 15px;
    letter-spacing: 0.4em;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 16px;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  &__item {
    margin-top: 23px; }

  &__item-title {
    font-family: $font-proxima-nova;
    font-size: 11px;
    line-height: 22px;
    font-weight: 700; }

  &__item {
    font-family: $font-proxima-nova;
    font-size: 11px;
    line-height: 22px;
    font-weight: 400; }

  &--mobile {
    .distribute__title {
      margin-bottom: 10px; }

    .distribute__item {
      margin-top: 0;

      &-job {
        margin-bottom: 22px; } } } }
