.button {
  display: inline-block;
  font-family: $font-gotham-pro;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.4em;
  color: #fff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border: 2px solid #fff;
  padding: 20px;
  outline: none;
  background: transparent;
  cursor: pointer;
  transition: 0.3s color, 0.3s background;

  &--full-width {
    width: 100%; }

  &--dark {
    background: #000;

    &:hover {
      background: #fff;
      color: #000; } }

  &--light {
    background: #fff;
    color: #000;

    &:hover {
      background: #000;
      color: #fff; } }

  &--transparent {
    background-color: transparent;

    &:hover {
      background-color: #fff;
      color: #000; } }

  &--default {
    padding: 0;
    border: none;
    background-color: transparent;
    transition: transform 0.4s;

    &:hover {
      transform: scale(1.3); } }

  &--oval {
    border-radius: 22px; }

  &--sm {
    padding: 17px 14px;
    border-width: 1px;
    font-size: $font-small; } }
