.laptop {

  li {
    float: left; }

  &__tabs {
    margin-left: 30%;
    padding: 0 8px;
    @include clearfix;

    //+media-1680
 }    //  margin-left: (100% / 12 * 3)

  &__tab {
    padding: 15px 20px;
    color: #fff;
    display: inline-block;
    border: 1px solid transparent;
    border-bottom: 0;
    cursor: pointer;
    margin-right: 18px;

    &--active {
      border-color: #fff;
      cursor: default; }

    &:hover {
      background: #0e0e0e; } }

  &__tab-number {
    display: inline-block;
    vertical-align: middle;
    font-family: $font-helvetica-nue-cyr;
    font-size: 36px;
    font-weight: 600;
    transform: rotate(-90deg); }

  &__tab-description {
    display: inline-block;
    vertical-align: middle;
    font-family: $font-proxima-nova;
    font-weight: 400;
    font-size: 11px;
    line-height: 1.5em; }

  &__tab-container {
    position: relative;
    margin-left: 8.5%;

    //+media-1680
 }    //  margin-left: 0

  &__tab-content {
    display: none; }

  &__bg {
    max-width: 112%;
    max-height: 100%;
    margin-right: -50px; }

  &__content-wrapper {
    position: absolute;
    top: 8%;
    left: 31%;
    width: 48%;
    height: 81%; }

  &__title {
    font-family: $font-proxima-nova;
    font-weight: 700;
    font-size: 36px;
    color: #fff;
    padding: 15px 0; } }
