.universe-grid-mobile {
  padding-top: 61px;

  &__title {
    padding: 10px 38px;
    box-sizing: border-box;
    background-color: #191919;
    font-size: 18px; }

  &__table {
    width: 100%;

    > tbody > tr > td {
      width: 50%;

      &:first-child {
        .universe-grid-mobile__taste {
          border-right: 1px solid #909090; } } } }

  &__taste {
    overflow: hidden;
    display: block;
    height: 157px;
    padding: 28px 7px 20px 28px;
    border-bottom: 1px solid #909090;
    box-sizing: border-box;
    transition: background-color 0.3s, color 0.3s;

    &:hover {
      background-color: $text-color;
      color: #000; }

    &-title {
      overflow: hidden;
      height: 38px;
      max-width: 100%;
      font-size: $font-medium;
      line-height: 18px;
      letter-spacing: 3px;
      text-transform: uppercase; }

    &-details {
      font-weight: $font-regular;
      font-size: 14px;
      line-height: 18px; } }

  &__footer {
    position: relative;
    height: 411px;
    margin-top: -14px; }

  &__dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 39%; }

  &__decoration {
    position: relative;
    overflow: hidden;
    height: 100%; }

  &__planet-lg {
    top: 0;
    left: -50px;
    width: 200px; }

  &__planet-md {
    width: 100px;
    top: 67px;
    right: -64px; }

  &__planet-sm {
    width: 70px;
    bottom: 86px;
    left: 49px; }

  &__planet-arc {
    position: absolute;
    top: 100%;
    left: 50%;
    width: 280px;
    margin-left: -140px;
    margin-top: -140px; } }
