.tobacco-mobile {
  padding-top: 61px;

  &__item {
    &-title {
      margin-bottom: 30px; }

    &-banner {
      width: 208px;
      height: 135px;
      margin: 29px auto 36px;

      > img {
        width: 100%;
        height: 100%;
        max-width: 100%; } }

    &-instruction {
      position: relative;
      margin-bottom: 50px;
      box-sizing: border-box;
      text-align: center;
      @include clearfix;

      &:last-child {
        margin-bottom: 30px; }

      &-icon {
        display: inline-block;
        width: 226px;
        height: 341px;

        > img {
          width: 100%;
          height: 100%;
          max-width: 100%; } }

      &-desc {
        max-width: 300px;
        margin: 0 auto; }

      &-step {
        position: relative;
        padding-left: 90px;
        margin-bottom: 30px;
        text-align: left; }

      &-number {
        position: absolute;
        top: 50%;
        left: 0;
        margin-top: -30px;
        letter-spacing: 3px;
        font-family: $font-helvetica-nue-cyr;
        font-size: 60px; }

      &-title {
        margin-bottom: 10px;
        font-size: $font-small;
        letter-spacing: 0.2px; }

      &-detail {
        color: $text-color-dark;
        font-size: $font-small;
        line-height: 16px; } } } }
