.mix-mobile-popup {

  &__diagram {
    text-align: center;
    margin-bottom: 30px;

    img {
      display: inline-block;
      max-width: 100%; } }

  &__legend {
    text-align: center;
    font-size: 0;
    @include clearfix; }

  &__legend-item {
    display: inline-block;
    width: 30%;
    text-align: left;

    &:first-child {
      margin-left: 10%; } }

  &__item-img {
    margin-bottom: 20px; }

  &__item-descr {
    font-family: 'ProximaNova', sans-serif;
    font-size: 10px;
    font-weight: $font-regular;
    line-height: (22 / 10); } }
