.chart {
  position: relative;
  overflow: hidden;
  height: 100%;

  &__title {
    font-family: 'ProximaNova', sans-serif;
    font-weight: $font-regular;
    font-size: 36px;
    color: #fff;
    text-align: center;
    padding-top: 80px; }

  &__dots {
    position: absolute;
    bottom: 9%;
    left: 2.5%;
    width: 61%;
    padding-top: 19%;
    clip: rect(0px, 0px, 0px, 0px);
    opacity: 1;
    //transition: opacity 1s
    background: url("/images/dots.jpg");
    background-size: cover;
    @include media-1366 {
      left: 8.5%; }
    @include media-1280 {

 }      //left: 52px
    @include media-800h {
      width: 56%;
      padding-top: 17%; }
    //&--show
    //  transition: all 3.6s
 }    //  width: 61%

  &__calendar {
    position: absolute;
    width: 557px;
    height: 199px;
    bottom: 0%;
    margin-bottom: 11.5%;
    opacity: 1;
    @include media-1366 {
      margin-bottom: 10.8%; }
    @include media-800h {
      margin-bottom: 7.5%; }
    a {
      opacity: 0; }
    div {
      opacity: 0; }
    &--show {
      animation: {
        name: calendar-show;
        duration: 1s;
        iteration-count: 1;
        fill-mode: forwards; } }

    &-month {
      position: absolute;
      z-index: 520;
      padding-top: 22px;
      letter-spacing: 3px;
      text-transform: uppercase;
      transition: text-shadow 0.3s;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        width: 13px;
        height: 13px;
        border: 1px solid #fff;
        border-radius: 100%;
        margin-left: -6.5px;
        box-sizing: border-box;
        background-color: #000; }

      //&:hover
      //  text-shadow: 1px 1px 6px #fff

      &--top {
        padding-top: 0;
        padding-bottom: 22px;

        &:after {
          top: auto;
          bottom: 0; } }

      &--left {
        &:after {
          left: 2.5px; } }

      &:hover:after {
        // box-shadow: 0 0 24px 1px #fff
        background-color: #fff;
        transition: background-color 0.3s; }

      &-1 {
        left: -2.5%;
        bottom: 8.2%;

        //&:hover~.chart__calendar-line-1
 }        //  box-shadow: 0 0 24px 1px #fff

      &-2 {
        left: 10.9%;
        top: 51%;

        //&:hover~.chart__calendar-line-2
 }        //  box-shadow: 0 0 24px 1px #fff

      &-3 {
        top: 28%;
        left: 21.6%;

        //&:hover~.chart__calendar-line-3
 }        //  box-shadow: 0 0 24px 1px #fff

      &-4 {
        top: 10.5%;
        right: 58.1%;

        //&:hover~.chart__calendar-line-4
 }        //  box-shadow: 0 0 24px 1px #fff

      &-5 {
        top: 34%;
        right: 50.9%;

        //&:hover~.chart__calendar-line-5
 }        //  box-shadow: 0 0 24px 1px #fff

      &-6 {
        top: 0.9%;
        right: 42.3%; }


      &-7 {
        top: 18.5%;
        right: 31%; }


      &-8 {
        top: -9.5%;
        right: 20.1%; }


      &-9 {
        top: 13.5%;
        right: 8.1%; }

      &-10 {
        top: -7.5%;
        right: -2.9%; }

      &-11 {
        top: 16.5%;
        right: -16.9%; }

      &-12 {
        top: -13.5%;
        right: -21.7%; } }
    //&:hover~.chart__calendar-line-6
    //  //  box-shadow: 0 0 24px 1px #fff

    &-line {
      position: absolute;
      z-index: 510;
      height: 1px;
      background-color: #fff;
      transition: box-shadow 0.3s;

      &-1 {
        bottom: 67px;
        left: 10px;
        width: 56px;
        transform: rotate(-48deg); }

      &-2 {
        top: 46.6%;
        left: 12.1%;
        width: 68px;
        transform: rotate(-17deg); }

      &-3 {
        top: 32%;
        left: 25.6%;
        width: 60px;
        transform: rotate(-30deg); }

      &-4 {
        top: 30.4%;
        right: 54.3%;
        width: 47px;
        transform: rotate(27deg); }

      &-5 {
        top: 25%;
        right: 45.3%;
        width: 50px;
        transform: rotate(-52deg); }

      &-6 {
        top: 17.6%;
        right: 35.8%;
        width: 52px;
        transform: rotate(14deg); }

      &-7 {
        top: 12.6%;
        right: 25%;
        width: 56px;
        transform: rotate(-36deg); }

      &-8 {
        top: 9.5%;
        right: 15.8%;
        width: 47px;
        transform: rotate(25deg); }

      &-9 {
        top: 10.5%;
        right: 4.1%;
        width: 59px;
        transform: rotate(-17deg); }

      &-10 {
        top: 12%;
        right: -5.8%;
        width: 47px;
        transform: rotate(27deg); }

      &-11 {
        top: 10%;
        right: -15.4%;
        width: 60px;
        transform: rotate(-37deg); } } }

  &__decoration {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &-hand {
      position: absolute;
      bottom: 10%;
      z-index: 3;
      opacity: 1;
      left: 63.8%;
      max-width: 17.2%;
      animation: {
        name: hand-shake;
        duration: 2.5s;
        iteration-count: 1;
        timing-function: linear; }
      transform-origin: 100% 100%; }
    &-hand2 {
      position: absolute;
      bottom: 10%;
      left: 63.8%;
      max-width: 17.2%;
      transform-origin: 100% 100%;
      opacity: 0;
      //@media screen and (max-width: 1280px) and (max-height: 1024px)
 } } }      //  left: 66.8%
