.navigation {
  z-index: 4000;
  position: absolute;
  top: 27.5%;
  left: 0;
  overflow: hidden;

  @include media-992 {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    transition: visibility 0s 0.3s; }

  &--open {
    @include media-992 {
      visibility: visible;
      transition: visibility 0s 0s; }

    .navigation__content {
      @include media-992 {
        transform: translateX(0); } } }

  &__content {
    @include media-992 {
      position: relative;
      width: 100%;
      height: 100%;
      padding-top: 86px;
      background-color: #000;
      transform: translateX(100%);
      transition: transform 0.3s; } }

  &__list {
    &--desctop {
      position: fixed;
      @include media-992 {
        display: none; } }

    &--mobile {
      display: none;

      @include media-992 {
        display: block; } }

    & > li {
      margin-bottom: 12px;

      @include media-992 {
        margin-bottom: 25px; }

      &:last-child {
        margin-bottom: 1px; } }

    & > li > a {
      line-height: 18px;
      letter-spacing: 3.8px;
      text-transform: uppercase;

      @include media-992 {
        font-size: 20px;
        line-height: 28px;
        letter-spacing: 0;
        text-transform: none; } } }

  &__dots {
    display: none;

    @include media-992 {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 50%; } } }
