.switcher {

  &__navigation {
    width: 400px;
    // margin: 0 auto 61px
    display: inline-block;
    vertical-align: middle;

    @include media-992 {
      display: block;
      width: 285px;
      margin: 0 auto 44px; } }

  &__scale {
    padding-left: 20px;
    margin-bottom: 16px;

    @include media-992 {
      padding-left: 0; }

    > li {
      display: inline-block;
      width: 1px;
      margin-right: 20px;
      background-color: #535353;
      vertical-align: bottom;
      transition: background-color 0.3s;

      @include media-992 {
        margin-right: 15.4px; }

      &.active {
        background-color: $text-color; }

      &:last-child {
        margin-right: 0; } } }

  &__list {
    @include clearfix;
    text-align: center; }

  &__item {
    letter-spacing: 4px;
    text-transform: uppercase;

    &-soft {
      float: left; }

    &-rear {
      float: right; } }

  &__tabs {
    width: 52%;
    //margin: 0 auto
    display: inline-block;
    vertical-align: middle;
    //margin-left: 15%
    padding-left: 8%;

    @include media-992 {
      display: block;
      width: 80%;
      margin: 0 auto;
      text-align: center; } }

  &__tab {
    display: none;
    opacity: 0;
    transition: opacity 0.3s;

    p {
      font-family: $font-proxima-nova;
      font-size: 16px;
      line-height: (31 / 16);
      letter-spacing: 0.015em; }

    &--animate {
      display: block;
      opacity: 0; }

    &--active {
      display: block;
      opacity: 1; } } }
