.mix-popup {

  .mfp-arrow {
    display: none; }

  &__content {
    @include clearfix; }

  &__diagram {
    width: 55%;
    float: left;
    padding: 0 8px; }

  &__info {
    float: left;
    padding: 0 8px;
    width: 45%; }

  &__numbers {
    margin-bottom: 45px;

    span {
      display: inline-block;
      padding: 15px;
      border: 1px solid #fff;
      border-radius: 50%;
      line-height: 0.6;
      font-family: $font-proxima-nova;
      font-size: 24px;
      font-weight: 700;
      color: #fff;
      width: 45px;
      height: 45px;
      text-align: center;
      vertical-align: middle;
      margin-right: 20px;
      transition: .3s all;
      cursor: pointer;

      &:hover {
        color: #191919;
        background: #fff; } } }

  &__name {
    font-family: $font-proxima-nova;
    font-weight: 700;
    font-size: 24px;
    margin-bottom: 45px; }

  &__consist {

    li {
      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 0; } }

    img {
      vertical-align: middle;
      margin-right: 20px; }

    span {
      vertical-align: middle;
      font-family: $font-proxima-nova;
      font-size: 11px;
      font-weight: 400; } } }
