.accessories {
  position: relative;
  height: 100%;

  &__item {
    cursor: pointer; }

  &__slide1-dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 35%; }

  &__slide2-dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 25%; }

  &__decorations {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &-mine {
      position: absolute;
      top: 12.9%;
      left: 17.6%;
      width: 22.6%;
      //animation:
      //name: mine-flight
      //duration: 6s
      //timing-function: linear
 }      //iteration-count: 1

    &-flask {
      position: absolute;
      top: 20.4%;
      right: 37.4%;
      width: 9.5%;
      //animation:
      //name: flask-flight
      //duration: 6s
      //timing-function: linear
 }      //iteration-count: 1

    &-hose {
      position: absolute;
      bottom: 10.7%;
      right: 22.3%;
      width: 13%;
      //animation:
      //name: hose-flight
      //duration: 6s
      //timing-function: linear
 }      //iteration-count: 1

    &-forceps-right {
      position: absolute;
      top: 14.3%;
      right: 7%;
      width: 10.1%;
      //animation:
      //name: forceps-right-flight
      //duration: 6s
      //timing-function: linear
 }      //iteration-count: 1

    &-spaceman-back {
      position: absolute;
      bottom: 0;
      left: 38%;
      width: 13.8%; }

    &-hookah {
      position: absolute;
      top: 13.3%;
      left: 31.1%;
      width: 31.2%; }

    &-forceps-left {
      position: absolute;
      top: 10.1%;
      right: 32.3%;
      width: 10.7%;
      //animation:
      //name: forceps-left-flight
      //duration: 10s
      //timing-function: linear
 }      //iteration-count: infinite

    &-spaceman-side {
      position: absolute;
      bottom: 3.3%;
      right: 20.3%;
      //width: 9%
      width: 11%; } }

  &__container {
    padding: 32px 40px 31px 39px;
    background-color: #191919; }

  &__carousel {

    &-item {
      display: table;
      padding-right: 53px;
      padding-left: 36px; }

    &-banner {
      display: table-cell;
      padding-right: 115px;
      padding-left: 21px; }

    &-icon {
      width: 165px;
      height: 485px;
      vertical-align: middle;

      img {
        height: 100%;
        max-width: 100%; } }

    &-details {
      display: table-cell;
      vertical-align: middle; }

    &-title {
      margin-bottom: 40px;
      font-weight: $font-bold;
      font-size: 23px;
      letter-spacing: 0.4px; }

    &-description {
      margin-bottom: 28px; }

    &-property {
      margin-bottom: 48px;

      > li {
        margin-bottom: 12px;
        font-size: $font-small;
        letter-spacing: 0.5px;

        &:last-child {
          margin-bottom: 0; } }

      &-value {
        font-weight: $font-regular; } } } }
