.taste {

  &__container {
    padding: 37px 40px 35px 39px;
    background-color: #191919; }

  &__carousel {
    &-item {
      display: table; }

    &-banner {
      display: table-cell;
      padding-right: 60px;
      padding-left: 72px;
      vertical-align: middle; }

    &-icon {
      width: 432px;
      height: 476px;

      img {
        height: 100%;
        max-width: 100%; } }

    &-details {
      display: table-cell;
      padding-right: 100px;
      vertical-align: middle; }

    &-title {
      font-weight: $font-bold;
      font-size: 23px;
      letter-spacing: 0.4px; }

    &-description {
      margin-top: 40px; } } }
