.map-mixes-mobile {
  padding-top: 61px;

  &__carousel {}

  &__item {}


  &__item-title {}


  &__item-list {
    padding-top: 20px;
    // padding-bottom: 25px
    text-align: center; }

  &__item-mix {
    display: inline-block;
    width: 117px;
    height: 115px;
    padding: 10px;
    border: 1px solid #909090;
    margin: 0 5.5px 50px;
    font-weight: $font-regular;
    font-size: 14px;
    text-align: left;
    font-family: 'ProximaNova', sans-serif;
    line-height: ( 21 / 14 );
    transition: background-color 0.3s, color 0.3s;
    vertical-align: top;


    &:hover {
      background-color: $text-color;
      color: #000; }

    &-number {}

    &-title {}

    &-dots {} }

  &__footer {
    position: relative;
    margin-top: -20px; }

  &__dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%; }

  &__decoration {
    position: relative;
    padding-top: 48%;
    overflow: hidden;

    &-planet {
      width: 25%;
      top: 0;
      left: 12%; }

    &-planet-arc {
      width: 90%;
      top: 12%;
      left: 50%;
      transform: translateX(-50%); } } }
