.universe-planet {
  position: relative;
  overflow: hidden;
  height: 100%;

  &__decoration {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    &-spaceman {
      position: absolute;
      top: 23%;
      left: -23.3%;
      width: 23.3%;
      animation: {
        name: universe-spaceman-flight;
        duration: 25s;
        iteration-count: infinite;
        timing-function: linear; } } }

  &__dots {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 65%;
    width: 25%; }

  &__container {
    position: relative;
    height: 100%;
    z-index: 1; }

  &__taste {
    position: absolute;
    width: 3.204vw;
    height: 3.204vw;
    border-radius: 100%;
    background: no-repeat center;
    background-size: cover;
    opacity: 0;

    &-1 {
      left: 13.9%;
      bottom: 22.5%;
      animation: {
        name: taste-12-flight;
        duration: 7s;
        iteration-count: infinite;
        timing-function: linear; } }

    &-2 {
      top: 38.3%;
      left: 27%;
      animation: {
        name: taste-12-flight;
        delay: 2s;
        duration: 10s;
        iteration-count: infinite;
        timing-function: linear; } }

    &-3 {
      bottom: 21%;
      left: 33%;
      animation: {
        name: taste-3-flight;
        delay: 3s;
        duration: 15s;
        iteration-count: infinite;
        timing-function: linear; } }

    &-4 {
      bottom: 1%;
      left: 57%;
      animation: {
        name: taste-4-flight;
        duration: 10s;
        iteration-count: infinite;
        timing-function: linear; } }

    &-5 {
      top: 45%;
      right: 34.1%;
      animation: {
        name: taste-5-flight;
        duration: 6s;
        iteration-count: infinite;
        timing-function: linear; } }

    &-6 {
      top: 48%;
      right: 14.8%;
      animation: {
        name: taste-67-flight;
        delay: 1s;
        duration: 10s;
        iteration-count: infinite;
        timing-function: linear; } }

    &-7 {
      top: 19%;
      right: 12.8%;
      animation: {
        name: taste-67-flight;
        duration: 16s;
        iteration-count: infinite;
        timing-function: linear; } } } }
