&__about {
  @include clearfix;

  &-description {
    margin-top: 30px;

    p {
      font-family: $font-proxima-nova;
      font-size: 16px;
      line-height: (31 / 16);
      letter-spacing: 0.015em; } }

  &-banner {
    padding: 0 20px;
    margin: 29px auto 36px;

    > img {
      max-width: 100%; } }

  &-dots {
    position: relative;
    width: 514px;
    height: 232px; } }

&__about-right, &__about-left {
  float: left; }

&__about-left {
  width: 51%; }

&__about-right {
  width: 49%;
  text-align: right; }
