.universe-grid {
  padding-top: 103px;

  &__dots {
    position: absolute;
    top: 35%;
    right: 0;
    width: 28%;
    height: 65%; }

  &__container {
    width: 574px;
    margin: 0 auto; }

  &__table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 13px; }

  &__taste {
    overflow: hidden;
    display: block;
    width: 174px;
    height: 174px;
    padding: 19px 16px;
    border: 1px solid $text-color;
    transition: background-color 0.3s, color 0.3s;

    &:hover {
      background-color: $text-color;
      color: #000; }

    &-title {
      margin-bottom: 10px;
      font-family: $font-gotham-pro;
      font-weight: $font-bold;
      font-size: $font-small;
      letter-spacing: 4px;
      text-transform: uppercase; }

    &-details {
      line-height: 22px; } } }
