.form {
  background: #191919;
  padding: 24px 19px 20px 18px;

  &__title {
    margin-bottom: 27px;
    font-family: $font-proxima-nova;
    font-weight: 700;
    font-size: 24px;
    letter-spacing: -0.5px; }

  #{$all-text-inputs} {
    width: 100%;
    border: none;
    margin: 0;
    padding: 9px 18px 10px;
    background: #252525;
    color: #fff;
    font-family: $font-proxima-nova;
    font-weight: 700;
    font-size: 10px;
    line-height: 2em;
    letter-spacing: 0.4em;
    text-transform: uppercase;
    outline: none;
    margin-bottom: 18px;
    text-align: left;

    &::placeholder {
      color: #fff; }

    &:focus {
      &::placeholder {
        color: #454545;
        transition: 0.2s color; } } }

  [type="submit"] {
    display: inline-block;
    padding: 16px 20px;
    outline: none;
    user-select: none;
    text-transform: uppercase;
    background: transparent;
    flex-flow: $font-gotham-pro;
    font-size: 10px;
    letter-spacing: 0.4em;
    color: #fff;
    padding: 16px 20px;
    border: 1px solid #fff;
    border-radius: 50px;
    cursor: pointer;

    &:hover {
      background: #fff;
      color: #191919;
      transition: 0.2s all; } }

  textarea {
    resize: none; }

  &__footer {
    text-align: center; }

  &--mobile {
    padding-top: 19px;
    padding-bottom: 45px;

    #{$all-text-inputs} {
      margin-bottom: 29px; }

    .form__title {
      font-size: 19px;
      font-weight: 700;
      line-height: 30px;
      letter-spacing: -.4px; }

    .form__footer {
      margin-top: 17px; }

    [type="submit"] {
      padding: 18px 34px; } } }
