$star-degree: 135deg;
$star-duration: 8s;

.starfall {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  &__star {
    position: absolute;
    animation: {
      name: star-flay;
      duration: $star-duration;
      timing-function: linear;
      iteration-count: infinite; }

    &-line {
      height: 2px;
      background: #fff;
      width: 200px;
      transform-origin: 0 0;
      animation: {
        name: star-drop;
        duration: $star-duration;
        timing-function: linear;
        iteration-count: infinite; } } } }

@keyframes star-flay {
  0% {
    transform: rotate($star-degree) translate(0,0); }
  100% {
    transform: rotate($star-degree) translate(200px,0); } }

@keyframes star-drop {
  0% {
    transform: scaleX(0); }
  70% {
    transform: scaleX(1);
    transform-origin: 0 0; }
  70.1% {
    transform: scaleX(1);
    transform-origin: 100% 0; }
  100% {
    transform: scaleX(0);
    transform-origin: 100% 0; } }
