.modal-mobile {
  z-index: 6000;
  background: #000;

  .mfp-close {
    color: #fff;
    font-size: 0;
    width: 60px;
    height: 60px;

    &:after, &:before {
      content: "";
      display: block;
      height: 3px;
      background: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 50%; }

    &:before {
      transform: translateX(-50%) rotateZ(45deg); }

    &:after {
      transform: translateX(-50%) rotateZ(-45deg); } }

  .mfp-container {
    padding: 0; }

  .mfp-content {
    height: 100%; } }
