.video-js {
  .vjs {

    &-big-play-button {
      position: absolute;
      top: 50%;
      left: 50%;
      background-color: transparent;
      border: none;
      width: 160px;
      height: 68px;
      margin: {
        top: -35px;
        left: -80px; }
      transition: transform 0.3s;

      &:hover,
      &:focus {
        background-color: transparent;
        transform: scale(1.1);
        transition: transform 0.3s; }

      &:before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        font-size: 27px;
        line-height: 1;
        text-align: center; }

      &:after {
        content: 'Смотреть видео';
        position: absolute;
        bottom: 9px;
        left: 0;
        width: 100%;
        font: {
          family: $font-gotham-pro;
          weight: $font-bold;
          size: $font-small; }
        line-height: 1;
        letter-spacing: 4px;
        text-align: center;
        text-transform: uppercase; } } }

  &:hover {
    .vjs-big-play-button {
      transition: transform 0.3s;
      background-color: transparent; } } }
