.events-mobile {
  margin-top: 61px;

  &__carousel {}

  &__item {

    &-group {
      margin-bottom: 30px;

      &--center {
        text-align: center; } }

    &-title {
      margin-bottom: 20px; }

    &-banner {
      width: 222px;
      margin: 0 auto 33px;
      text-align: left; }

    &-holder {
      width: 100%;
      height: 222px; }

    &-icon {
      width: 100%;
      height: 100%;
      min-width: 100%; }

    &-address {
      margin-top: 22px;

      &-link {
        display: inline-block;
        padding-left: 20px;
        background: url('../images/icon-mark.svg') no-repeat left center;
        background-size: 12px 16px;
        font-weight: $font-regular;
        font-size: 11px;
        line-height: 16px;
        letter-spacing: 0.3px; } }

    &-header {
      margin-bottom: 34px; }

    &-event-title {
      font-size: 18px; }

    &-date {
      display: block;
      margin-top: 11px;
      font-weight: $font-regular;
      font-size: 11px;
      letter-spacing: 4px;
      text-transform: uppercase; }

    &-description {
      margin-bottom: 36px; }

    &-video {
      width: 222px;
      height: 142px;
      margin: 0 auto 40px;
      background-color: #252525;

      &-player {
        width: 100%;
        height: 100%;
        max-width: 100%;
        max-height: 100%; } }

    &-social {
      & > li {
        display: inline-block;
        margin-right: 18px;
        font-size: 0;

        &:last-child {
          margin-right: 0; } }

      %social-icon-mob {
        display: inline-block;
        background: {
          repeat: no-repeat;
          position: center;
          size: cover; }
        vertical-align: bottom;
        transition: transform 0.3s;

        &:hover {
          transform: scale(1.3); } }

      &-fb {
        @extend %social-icon-mob;
        width: 8px;
        height: 18px;
        background-image: url('../images/icon-fb.svg'); }

      &-vk {
        @extend %social-icon-mob;
        width: 20px;
        height: 11px;
        background-image: url('../images/icon-vk.svg'); }

      &-insta {
        @extend %social-icon-mob;
        width: 18px;
        height: 18px;
        background-image: url('../images/icon-insta.svg'); } }

    &-photo-show {
      margin-bottom: 20px; } }

  &__footer {
    position: relative;
    height: 262px; }

  &__dots {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

  &__decoration {
    position: relative;
    overflow: hidden;
    height: 100%; }

  &__planet-sm {
    position: absolute;
    width: 66px;
    top: 27px;
    right: 20px; }

  &__planet-lg {
    position: absolute;
    width: 200px;
    left: -60px;
    bottom: -38px; } }
