.footer {
  z-index: 4000;
  position: fixed;
  left: 0;
  bottom: 20px;
  width: 100%;
  visibility: hidden;

  @include media-992 {
    display: none; }

  &__navigation {
    position: absolute;
    bottom: 0;
    visibility: visible;

    & > li {
      display: inline-block;
      margin-right: 48px;
      font-size: 0;
      vertical-align: bottom;

      &:last-child {
        margin-right: 0; } }

    & > li > a {
      color: $text-color-dark;
      font-size: $font-small;
      letter-spacing: 4px;
      text-transform: uppercase;

      &:after,
      &:before {
        bottom: -3px;
        background-color: $text-color-dark; } } }

  &__appendix {
    float: right;
    visibility: visible;

    &-link {
      display: inline-block;
      width: 96px;
      height: 96px;
      background: url('../images/footer-icon.png') no-repeat center;
      transition: transform 0.5s;

      &:hover {
        transform: scale(1.1); } } } }
