.link {
  &--underline {
    position: relative;
    display: inline-block;

    &:before,
    &:after {
      content: '';
      position: absolute;
      bottom: -1px;
      width: 0;
      height: 1px;
      background-color: #fff;
      transition: width 0.5s;

      @include media-992 {
        height: 2px; } }

    &:before {
      right: 0; }

    &:after {
      left: 0; }

    &:hover:before,
    &:hover:after {
      width: 50%; } }

  &--active {
    &:before,
    &:after {
      width: 50%; } } }
