.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  display: table;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s opacity, visibility 0s 0.3s;

  @include media-992 {
    display: block;
    background-color: #000; }

  &__container {
    display: table-cell;
    vertical-align: middle;

    @include media-992 {
      display: block;
      height: 100%; } }

  &__dialog {
    position: relative;
    margin: 0 auto;

    @include media-992 {
      overflow: auto;
      height: 100%;
      padding-top: 61px;
      margin: 0; } }

  &__close {
    position: absolute;
    top: 62px;
    right: 22px;
    z-index: 10100;
    width: 17px;
    height: 17px;
    background: url('../images/icon-cross.svg') no-repeat center;

    @include media-992 {
      top: 21.5px; }

    &--top {
      top: 28px;
      right: 27px;

      @include media-992 {
        top: 21.5px; } } }

  &--md {
    .modal__dialog {
      max-width: 920px;

      @include media-992 {
        max-width: none; } } }

  &--open {
    opacity: 1;
    visibility: visible;
    transition: 0.3s opacity, visibility 0s 0s; } }
