.where-buy-mobile {
  @extend .universe-grid-mobile;

  &__title {
    @extend .universe-grid-mobile__title;
    padding-left: 20px; }

  &__table {
    @extend .universe-grid-mobile__table;

    border-collapse: collapse;
    border: {
      left: 2px solid #000;
      top: 2px solid #000;
      right: 2px solid #000; }

    td {
      padding: 19px 20px;
      border: 1px solid #808080; } }

  &__footer {
    position: relative;
    //height: 288px
    height: 750px;
    overflow: hidden;
    @include media-800 {
      height: auto;
      padding-top: 90%; } }


  &__dots {
    @extend .universe-grid-mobile__dots;
    height: 60%; }

  &__planet-1 {
    width: 65%;
    max-width: 526px;
    left: -5%;
    top: 7%; }

  &__planet-2 {
    width: 23%;
    right: -2%;
    bottom: 0; } }
