&__pack {

  &-content {
    // margin-top: 40px
    // margin-bottom: 100px
    padding: 95px 0 100px;
    @include clearfix; }

  &-instruction {
    position: relative;
    float: left;
    width: 50%;
    box-sizing: border-box;
    @include clearfix;

    &--left {
      padding-right: 9px;
      text-align: right;

      .tobacco__pack-instruction-desc {
        left: 0;
        top: 50%;
        transform: translate(0,-50%); }

      .tobacco__pack-instruction-step {
        padding-bottom: 20px; } }

    &--right {
      padding-left: 9px;

      .tobacco__pack-instruction-desc {
        width: 190px;
        top: 50%;
        right: 0;
        transform: translate(0,-44.5%); }

      .tobacco__pack-instruction-step:after {
        right: 105%;
        transform-origin: 100% 0; } }

    &-icon {
      display: inline-block;
      width: 195px;
      height: 298px;

      > img {
        width: 100%;
        height: 100%;
        max-width: 100%; } }

    &-desc {
      position: absolute;
      z-index: 10100;
      width: 200px; }

    &-step {
      position: relative;
      padding: 10px 0 5px 50px;
      text-align: left;

      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 100%;
        height: 1px;
        background-color: $text-color; } }

    &-step-1 {
      &:after {
        right: -115px;
        transform-origin: 0 0;
        transform: rotate(25deg); } }

    &-step-2 {
      &:after {
        left: 95%;
        right: -100px; } }

    &-step-3 {
      &:after {
        right: -115px;
        transform-origin: 0 0;
        transform: rotate(-25deg); } }

    &-step-4 {
      &:after {
        right: 107% !important;
        top: 62%;
        left: -125px;
        transform-origin: 0 0;
        transform: rotate(-25deg); } }

    &-step-5 {
      &:after {
        left: -115px; } }

    &-step-6 {
      &:after {
        top: 8%;
        left: -115px; } }

    &-step-7 {
      &:after {
        right: 107% !important;
        top: -10px;
        left: -125px;
        transform-origin: 0 0;
        transform: rotate(25deg); } }


    &-number {
      position: absolute;
      left: 0;
      letter-spacing: 3px;
      margin-left: -13px;
      font-family: $font-proxima-nova;
      font-size: 48px;
      transform: rotate(-90deg); }

    &-title {
      margin-bottom: 7px;
      font-size: 13px;
      letter-spacing: 0.2px; }

    &-detail {
      color: #fff;
      opacity: 0.2;
      font-size: 13px;
      font-weight: 400;
      line-height: (22 / 13); } } }
