$tab-color: #191919;
$tab-button-color: #0e0e0e;
$tab-button-color-active: $tab-color;

.tabs {
  font-size: 0;

  &__navigation {
    &-button {
      overflow: hidden;
      display: inline-block;
      width: 168px;
      padding: 14px 10px;
      margin-right: 20px;
      background-color: $tab-button-color;
      font-size: $font-small;
      letter-spacing: 4px;
      text-align: center;
      text-transform: uppercase;
      text-overflow: ellipsis;
      white-space: nowrap;
      transition: background-color 0.3s;

      &--active {
        background-color: $tab-button-color-active; }

      &:hover {
        background-color: $tab-button-color-active; }

      &:last-child {
        margin-right: 0; } } }

  &__content {
    font-size: $font-small;
    background-color: $tab-color; }

  &__item {
    display: none;
    padding: 42px 75px 37px 38px;
    background-color: $tab-color;
    opacity: 0;
    transition: opacity 0.3s;

    &--animate {
      display: block;
      opacity: 0; }

    &--active {
      display: block;
      opacity: 1; }

    &--paddings-sm {
      padding-top: 26px;
      padding-right: 15px; }

    &--paddings-same {
      padding-top: 26px;
      padding-right: 39px;
      padding-left: 39px; } } }
