.accessories-mobile {
  margin-top: 61px;

  &__item {
    &-title {
      margin-bottom: 43px; }

    &-banner {
      width: 100%;
      margin-bottom: 37px;
      text-align: center;

      img {
        max-width: 100%;
        max-height: 485px; } }

    &-description {
      margin-bottom: 28px; }

    &-property {
      margin-bottom: 64px;

      > li {
        margin-bottom: 12px;
        font-size: $font-small;
        letter-spacing: 0.5px;

        &:last-child {
          margin-bottom: 0; } }

      &-value {
        font-weight: $font-regular; } } }

  &__dots {
    position: relative;
    height: 163px;
    margin: 0 20px; } }
