.events {
  overflow: hidden;
  height: 100%;

  &__scene {
    position: relative;
    height: 100%; }

  &__dots {
    position: absolute;
    height: 30%;
    width: 100%;
    top: 44%;
    left: 0; }

  &__decorations {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

  &__planet-lg {
    left: 30.7%;
    top: 7%;
    width: 183px;
    height: 183px; }

  &__planet-sm {
    top: 14.5%;
    right: 9.5%;
    width: 107px;
    height: 107px; }

  &__spaceman {
    position: absolute;
    right: 25.1%;
    bottom: 4.4%;
    width: 13.4%; }

  &__surface {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    max-width: 100%; }

  &__sphere {
    position: absolute;
    top: 75px;
    left: 50%;
    width: 298px;
    margin-left: -149px;
    font-size: 0;

    &-icon {
      width: 100%;
      max-width: 100%; }

    &-line {
      position: absolute;
      top: 50%;
      left: 100%;
      padding: 0 3px 9px 48px;
      margin-top: -20px;
      font-size: $font-small;
      text-transform: uppercase;
      letter-spacing: 2.6px;
      opacity: 0;
      visibility: hidden;
      transition: 0.7s opacity;

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 1px;
        background-color: #fff;
        transition: 0.5s width; } }

    &--active {
      .events__sphere-line {
        opacity: 1;
        visibility: visible;

        &:after {
          width: 100%; } } } }

  &__item {
    @include clearfix;

    &-panel {
      width: 50%;
      box-sizing: border-box;

      &--left {
        float: left;
        padding-right: 27px; }

      &--right {
        float: right;
        padding-left: 27px; } }

    &-banner {
      width: 100%;
      height: 336px;
      margin-bottom: 32px;
      background-color: #252525; }

    &-icon {
      width: 100%;
      height: 100%;
      max-width: 100%; }

    &-footer {
      @include clearfix; }

    &-address {
      float: left;
      display: table;
      height: 46px;

      &-link {
        display: table-cell;
        padding-left: 24px;
        background: url('../images/icon-mark.svg') no-repeat left center;
        background-size: 12px 16px;
        font-weight: $font-regular;
        font-size: 11px;
        letter-spacing: 0.1px;
        vertical-align: middle; } }

    &-book {
      float: right; }

    &-header {
      padding-top: 1px;
      margin-bottom: 38px; }

    &-date {
      display: block;
      margin-top: 15px;
      font-weight: $font-regular;
      letter-spacing: 3.7px;
      text-transform: uppercase; }

    &-video {
      width: 265px;
      height: 177px;
      margin-top: 47px;
      background-color: #252525;

      &-player {
        width: 100%;
        height: 100%;
        max-width: 100%;
        max-height: 100%; } }

    &-photo-show {
      margin-top: 34px; }

    &-social {
      position: absolute;
      right: 21px;
      bottom: 40px;

      & > li {
        margin-bottom: 27px;
        text-align: center;

        &:last-child {
          margin-bottom: 0; } }

      %social-icon {
        display: inline-block;
        background: {
          repeat: no-repeat;
          position: center;
          size: cover; }
        transition: transform 0.3s;

        &:hover {
          transform: scale(1.3); } }

      &-fb {
        @extend %social-icon;
        width: 8px;
        height: 18px;
        background-image: url('../images/icon-fb.svg'); }

      &-vk {
        @extend %social-icon;
        width: 20px;
        height: 11px;
        background-image: url('../images/icon-vk.svg'); }

      &-insta {
        @extend %social-icon;
        width: 18px;
        height: 18px;
        background-image: url('../images/icon-insta.svg'); } } } }
