.mix-slider {

  &__items-wrapper {
    @include clearfix; }

  &__item {
    float: left;
    width: (100% / 4);
    padding: 4px; }

  &__item-inner {
    border: 1px solid #fff;
    padding: 11px;
    padding-bottom: 15px;
    cursor: pointer;

    &:hover {
      background: #0e0e0e; } }

  &__item-number {
    font-family: $font-proxima-nova;
    font-size: 22px;
    font-weight: 400;
    color: #fff;

    @include media-1680 {
      font-size: 20px; }

    @include media-1440 {
      font-size: 18px; } }

  &__item-mix-name {
    font-family: $font-proxima-nova;
    font-size: 18px;
    font-weight: 400;
    line-height: 2em;
    color: #fff;

    @include media-1680 {
      font-size: 14px; }

    @include media-1440 {
      font-size: 11px; } }

  &__arrow {
    width: 36px;
    height: 36px;
    position: absolute;
    top: 50%;
    margin-top: -18px;

    &--prev {
      left: -50px; }

    &--next {
      right: -50px; } } }
