.planet {
  position: absolute;

  img {
    max-width: 100%; }

  &--one {
    width: 490px;
    left: 20%;
    top: 11%; }

  &--two {
    width: 324px;
    right: -100px;
    top: 37.5%; }

  &--three {
    width: 324px;
    top: 38%;
    left: 13%; }

  &--big-planet {
    left: 50%;
    top: 53%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 75%;

    img {
      width: 100%;
      max-width: inherit; } }

  &--for {
    top: -50px;
    left: 50%;
    transform: translateX(-50%); }

  &--five {
    width: 152px;
    top: 30%;
    left: 27%;

    img {
      filter: drop-shadow(1px 1px 1px rgba(255,255,255, 0.1)); } }

  &--six {
    width: 332px;
    right: -160px;
    top: 27%;

    img {
      filter: drop-shadow(1px 1px 1px rgba(255,255,255, 0.1)); } }

  &--light {
    box-shadow: 0 0 5px 1px rgba(255, 255, 255, 0.2);
    border-radius: 100%; } }
