.header {
  z-index: 5000;
  position: absolute;
  top: 18px;
  left: 0;
  width: 100%;

  @include media-992 {
    position: fixed;
    top: 0;
    padding: 18px 0 19px;
 }    //background-color: #000

  &--modal {
    position: static;
    background-color: #000; }

  &__langbar {
    padding: 7px 0 3px;
    font-size: 0;

    & > li {
      display: inline-block;
      margin-right: 32px;
      line-height: 1;
      vertical-align: top;

      @include media-992 {
        margin-right: 5px; }

      &:last-child {
        margin-right: 0; } }

    & > li > a {
      font-size: $font-small;
      letter-spacing: 4.5px;
      text-transform: uppercase;

      @include media-992 {
        font-size: 14px;
        letter-spacing: 2.5px; } } }

  &__banner {
    position: absolute;
    top: 0;
    left: 50%;
    width: 128px;
    height: 20px;
    margin-left: -64px;

    @include media-992 {
      top: 50%;
      margin-top: -10px; }

    &--enter {
      top: 40px; }

    &-link {
      display: inline-block;
      width: 100%;
      height: 100%; }

    &-icon {
      width: 100%;
      height: 100%; } }

  &__navigation-toggle {
    display: none;

    @include media-992 {
      position: absolute;
      top: 50%;
      right: 0;
      display: inline-block;
      width: 19px;
      height: 19px;
      padding: 0;
      border: none;
      margin-top: -9.5px;

      &:before,
      &:after,
      > span {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 3px;
        border-radius: 1px;
        background-color: #fff;
        transition: opacity 0.3s, transform 0.3s; }

      &:before {
        top: 0; }

      &:after {
        bottom: 0; }

      > span {
        top: 50%;
        margin-top: -1.5px; } }

    &--open {
      &:before {
        transform: translateY(8px) rotate(45deg); }

      &:after {
        transform: translateY(-8px) rotate(-45deg); }

      > span {
        opacity: 0; } } } }
