@import "popup/mfp";
@import "popup/mix-popup";
@import "popup/modal-mobile";
@import "popup/mix-mobile-popup";



.popup {
  background: #191919;
  padding: 60px 95px;
  text-align: left;
  max-width: 920px;
  margin: 40px auto;
  position: relative;

  .mfp-close {
    color: #fff;
    font-size: 0;
    width: 24px;
    height: 24px;
    top: 25px;
    right: 25px;

    &:after, &:before {
      content: "";
      display: block;
      height: 2px;
      width: 24px;
      background: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -12px; }

    &:before {
      transform: rotate(-45deg); }

    &:after {
      transform: rotate(45deg); } }

  &__arrow {
    width: 36px;
    height: 36px;
    position: absolute;
    top: 50%;
    margin-top: -18px;
    cursor: pointer;

    &--prev {
      left: 40px; }

    &--next {
      right: 40px; } } }
