@import "map-mixes/laptop";
@import "map-mixes/mix-slider";

.map-mixes {
  height: 100%;
  overflow: hidden;

  &__wrapper {
    height: 100%;
    transition: 2s transform;

    &--section-1 {
      transform: translateY(0); }

    &--section-2 {
      transform: translateY(-100%); } }

  &__section {
    display: table;
    height: 100%;
    width: 100%;
    position: relative;
    overflow: hidden; }

  &__inner {
    display: table-cell;
    vertical-align: middle;

    &--bottom {
      vertical-align: bottom; } }

  &__space-man {
    position: absolute;
    opacity: 0;
    transition: 0.5s opacity;

    &--active {
      opacity: 1; }

    &--pos-1 {
      width: 12.7%;
      bottom: 50px;
      left: 43%; }

    &--pos-2 {
      width: 11.3%;
      bottom: 46px;
      left: 44%; }

    &--pos-3 {
      width: 8.9%;
      bottom: 39px;
      left: 44.7%; } } }
