.carousel.owl-carousel {
  .owl-nav {
    .owl-prev,
    .owl-next {
      position: absolute;
      top: 50%;
      width: 35px;
      height: 35px;
      border-radius: 100%;
      margin-top: -17.5px;
      background: #191919 no-repeat center;
      background-size: cover;
      transition: transform 0.5s;

      &.disabled {
        display: none; } }

    .owl-prev {
      left: 0;
      background-image: url('../images/arrow-left.svg');

      &:hover {
        transform: translate(-3px); } }

    .owl-next {
      right: 0;
      background-image: url('../images/arrow-right.svg');

      &:hover {
        transform: translate(3px); } } }

  .owl-dots {
    position: absolute;
    bottom: 9px;
    left: 0;
    width: 100%;
    font-size: 0;
    text-align: center; }

  .owl-dot {
    display: inline-block;
    width: 12px;
    height: 12px;
    border: 1px solid $text-color;
    border-radius: 100%;
    margin-right: 28px;
    background-color: transparent;
    vertical-align: top;
    transition: background-color 1s;

    &:last-child {
      margin-right: 0; }

    &.active {
      background-color: $text-color; } } }

.carousel__item-title {
  display: table;
  width: 100%;
  height: 90px;
  padding: 0 60px;
  background-color: #191919;

  &-line {
    display: table-cell;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    vertical-align: middle;

    &--text-left {
      text-align: left; } } }

.carousel.carousel--mobile.owl-carousel {
  .owl-nav {
    .owl-prev,
    .owl-next {
      top: 45px;
      margin-top: -17.5px; }

    .owl-prev {
      left: 17px; }

    .owl-next {
      right: 17px; } } }

.carousel.carousel--auto.owl-carousel {
  .owl-item {
    img {
      display: inline-block;
      width: auto; } } }
